.geekleagues-list {
    padding: 5vh 0;
}

.my-geekleagues {
    justify-content: space-around;
}

.my-geekleagues h4 {
    color: var(--color-white-cream);
    font-family: 'Audiowide';
}

.my-geekleagues h6 {
    color: var(--color-white-cream);
}

.geekleague-card-container {
    padding: 20px;
}

.geekleague-card {
    background-color: rgba(156, 0, 99, 0.8);
    padding: 10px;
    border-radius: 10px;
}

a.geekleagues-page-btn {
    margin: 20px 10px 10px 10px;
}

a.geekleagues-page-btn:hover {
    color: var(--color-purple-rp);
}